.react-resizable {
    position: relative;
    z-index: 1000;
  }
  .react-resizable-handle {
    position: absolute;
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-origin: content-box;
    box-sizing: border-box;
    background-image: url("data:image/svg+xml;base64,");
    background-position: bottom right;
    padding: 0 5px 5px 0;
  }
  .react-resizable-handle-se {
    bottom: 0;
    right: 0;
    cursor: se-resize;
  }
  
  /* demo-grid.css */
  /* Controls */
  
  .controls {
    margin: 30px -10px;
  }
  .control {
    position: relative;
    float: left;
    width: 33%;
    padding: 0 10px;
  }
  @media (max-width: 600px) {
    .control {
      float: none;
      width: auto;
      margin: 0 0 15px 0;
    }
    .control.layout {
      margin: 0;
    }
  }
  .control-icon {
    position: absolute;
    left: 10px;
    top: 0;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    z-index: 2;
    pointer-events: none;
  }
  .control-field {
    position: relative;
    padding-left: 40px;
    z-index: 1;
  }
  
  /* Grid */
  
  .grid {
    position: relative;
    max-width: 960px;
    margin: 0 -10px;
    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
  }
  .item {
    display: none;
    
    width: 100px;
    height: 100px;
     margin: 10px;
    z-index: 1;
    will-change: transform;
   
  }
  .item.muuri-item-positioning {
    z-index: 2;
  }
  .item.muuri-item-dragging,
  .item.muuri-item-releasing {
    z-index: 9999;
  }
  .item.muuri-item-dragging {
    cursor: move;
  
  }
  .item.muuri-item-dragging  .content{
    cursor: move;
    border-radius: 5px;
    border: none;
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086);
    

  }
  .item.muuri-item-hidden {
    z-index: 0;
  }
  .item.muuri-item-placeholder {
    z-index: 2;
    margin: 0;
    opacity: 0.5;
  }
  
  .item.h2 {
    height: 220px;
    line-height: 220px;
  }
  .item.w2 {
    width: 220px;
  }
  .item-content {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .content {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    margin: 1px;
    font-size: 14px;
    border-radius: 5px;
    -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
 
   
  }
  .content-header {
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    height: 40px;
    cursor: pointer;
    line-height: 40px;
    width: 100%;
    
}
  
